<template>
  <div id="paySuccess">
  	<commonHeader :title="'支付成功'"></commonHeader>
  	<div class="pay-header">
  		<img class="success-icon" src="../../assets/my_icon_payment_success@3x.png"/>
  		<div class="success-text">支付成功</div>
  	</div>
  	
  	<div class="pay-tips">
  		<div class="tips-title">温馨提示</div>
  		<div class="tips-content">支付成功</div>
  	</div>
  	
  	<div class="pay-btns">
  		<div class="btn btn-order" @click="toOrderList">查看订单</div>
  		<div class="btn btn-back" @click="linkTo('Index')">返回首页</div>
  	</div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
      	
      }
    },
    watch: {},
    created(){
      
    },
    methods: {
    	toOrderList(){
    		this.$router.replace({name:'OrderList'})
    	}
    }
  }
</script>

<style lang="scss">
  @import "../../sass/baseSass.scss";
  #paySuccess{
    min-height: 100%;
    .pay-header{
      margin-top: 0.8rem;
      text-align: center;
      .success-icon{
        width: 1.33rem;
        height: 1.33rem;
      }
      .success-text{
        font-size: 0.43rem;
        margin-top: 0.27rem;
        color: $label-color;

      }
    }
    .pay-tips{
      margin-top: 0.67rem;
      padding: 0 0.4rem;
      .tips-title{
        font-size: 0.4rem;
        color: #333;
      }
      .tips-content{
        font-size: 0.37rem;
        padding-top: 0.24rem;
        color: #666;
      }
    }
    .pay-btns{
      margin-top: 2.53rem;
      .btn{
        width: 9.2rem;
        height: 1.2rem;
        line-height: 1.17rem;
        border-radius: 0.6rem;
        font-size: 0.45rem;
        text-align:center;
        margin: 0 auto;
      }
      .btn-order{
        background: #fff;
        color: $label-color;
        border:1px solid $label-color;

      }
      .btn-back{
        margin-top: 0.4rem;
        @include bg-color;
        color: #fff;
      }
    }
  }

</style>



